<template>
  <div class="info">
    <router-link to="/" class="pageToHome">
      <p class="inWord animated delay-1s duration-1s fadeInUpBig">返回首页</p>
      <img
        src="../../assets/images/返回.png"
        width="30%"
        class="inPic animated delay-1s duration-1s fadeInUpBig"
      />
    </router-link>
    <div class="form animated delay-1s duration-1s fadeInUpBig">
      <div class="popup"></div>
      <div class="detail">
        <div class="head">报名表</div>
        <div class="content">
          <p>姓名：</p>
          <van-field
            v-model="info.stuname"
            size="large"
            maxlength="14"
            class="inputBox"
            input-align="center"
          />
          <br />
          <p>学号：</p>
          <van-field
            v-model="info.stuid"
            size="large"
            class="inputBox"
            input-align="center"
            type="number"
            maxlength="20"
            placeholder="例:2018210200"
          />
          <br />
          <p>班级：</p>
          <van-field
            v-model="info.classid"
            size="large"
            class="inputBox"
            input-align="center"
            type="number"
            maxlength="20"
            placeholder="例:01011801"
          />
          <br />
          <p>手机号：</p>
          <van-field
            v-model="info.telephone"
            size="large"
            maxlength="20"
            class="inputBox"
            type="number"
            input-align="center"
          />
          <br />
          <div class="select">
            <div class="select-left">
              <p>专业：</p>
              <van-field
                readonly
                clickable
                :value="collegeShow"
                placeholder="选择"
                @click="showPicker = true"
                class="inputBox"
                input-align="center"
              />
              <van-popup v-model="showPicker" get-container="body" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="columns2"
                  @cancel="showPicker = false"
                  @confirm="onConfirm"
                />
              </van-popup>
            </div>
            <div class="select-right">
              <p>年级：</p>
              <van-field
                readonly
                clickable
                :value="gradeShow"
                placeholder="选择"
                @click="showPicker2 = true"
                class="inputBox"
                input-align="center"
              />
              <van-popup v-model="showPicker2" get-container="body" position="bottom">
                <van-picker
                  show-toolbar
                  :columns="columns1"
                  @cancel="showPicker2 = false"
                  @confirm="onConfirm2"
                />
              </van-popup>
            </div>
          </div>
          <p>QQ号(选填)：</p>
          <van-field
            v-model="info.qid"
            type="number"
            size="large"
            class="inputBox"
            input-align="center"
            maxlength="20"
          />
          <br />
          <p>综合排名(选填)：</p>
          <van-field
            v-model="info.rank_com"
            size="large"
            class="inputBox"
            input-align="center"
            type="number"
            maxlength="5"
          />
          <br />
          <p>专业排名(选填)：</p>
          <van-field
            v-model="info.rank_spec"
            size="large"
            class="inputBox"
            input-align="center"
            type="number"
            maxlength="5"
          />
          <br />
          <p>已加入组织(选填)：</p>
          <van-field
            v-model="info.experience"
            size="large"
            type="textarea"
            autosize
            maxlength="100"
            class="inputArea"
            placeholder="例：XX部门/XX实验室/XX社"
          />
          <br />
          <p>爱好特长：</p>
          <van-field
            v-model="info.hobby"
            size="large"
            type="textarea"
            autosize
            maxlength="100"
            class="inputArea"
            placeholder="注：字数不大于100字"
          />
          <br />
        </div>
        <div class="submit">
          <van-button
            type="info"
            size="large"
            class="button"
            @click="submitInfo"
            :loading="openLoading"
          >点击提交</van-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: {
        stuname: "",
        stuid: "",
        classid: "",
        telephone: "",
        college: "",
        grade: "",
        qid: "",
        rank_com: "",
        rank_spec: "",
        experience: "",
        hobby: ""
      },
      collegeShow: "",
      gradeShow: "",
      showPicker: false,
      showPicker2: false,
      columns1: ["大一", "大二", "大三", "大四", "其他"],
      columns2: ["通信工程", "电子信息工程", "信息工程", "数媒", "其他"],
      openLoading: false
    };
  },
  methods: {
    onConfirm(value) {
      this.collegeShow = value;
      this.showPicker = false;
      switch (value) {
        case "通信工程":
          this.info.college = 1;
          break;
        case "电子信息工程":
          this.info.college = 2;
          break;
        case "信息工程":
          this.info.college = 3;
          break;
        case "数媒":
          this.info.college = 4;
          break;
        case "其他":
          this.info.college = 5;
      }
    },
    onConfirm2(value) {
      this.gradeShow = value;
      this.showPicker2 = false;
      switch (value) {
        case "大一":
          this.info.grade = 1;
          break;
        case "大二":
          this.info.grade = 2;
          break;
        case "大三":
          this.info.grade = 3;
          break;
        case "大四":
          this.info.grade = 4;
          break;
        case "其他":
          this.info.grade = 5;
      }
    },
    submitInfo() {
      this.openLoading = true;
      if (
        this.info.stuname == "" ||
        this.info.stuid == "" ||
        this.info.classid == "" ||
        this.info.telephone == "" ||
        this.info.college == "" ||
        this.info.grade == ""
      ){
        this.openLoading = false;
        return this.$toast.fail('请完善报名表')
      }
      if (this.info.qid == "") {
          this.info.qid = "44444444";
        }
      let url = "http://zblade.top/recruit/addInfo";
      let detail = JSON.stringify(this.info);
      this.info.qid = "";
      this.axios
        .post(url, detail, {
          timeout: 1000 * 30 ,headers:{'Content-Type': 'application/json'}
        })
        .then(
          response => {
            if (response.data.code == 200) {
              this.$toast.success("报名成功");
              this.openLoading = false;
              this.$router.push("/");
            } else if (response.data.code == 413) {
              this.$toast.success("你已提交");
              this.openLoading = false;
            } else {
              this.$toast.fail("提交失败");
              this.openLoading = false;
            }
          },
          () => {
            this.$toast.fail("请求异常");
            this.openLoading = false;
          }
        );
    }
  }
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
  margin: 0 auto;
}
.info {
  height: 100%;
  width: 100%;
  background: url("../../assets/images/表单背景.png") no-repeat;
  background-size: 100% 100%;
}
.pageToHome {
  width: 6.5rem;
  height: 3rem;
  position: absolute;
  top: 0.5rem;
  left: 0.5rem;
  z-index: 99;
  color: aliceblue;
  font-family: contentfont;
}
.inPic {
  position: absolute;
  left: 0.2rem;
  top: 0.5rem;
}
.inWord {
  position: absolute;
  left: 2.2rem;
  top: 1rem;
  font-weight: bold;
}
.form {
  width: 90%;
  height: 30rem;
  background-color: white;
  border-radius: 1.5rem;
  position: absolute;
  top: 5rem;
  left: 1rem;
  overflow-y: scroll;
}
.detail {
  width: 100%;
  height: 40rem;

  font-family: contentfont;
}
.head {
  margin-bottom: 1rem;
  padding-top: 0.2rem;
  width: 80%;
  height: 2.2rem;
  font-size: 1.7rem;
  line-height: 1.7rem;
  text-align: center;
  border-bottom: 1px solid black;
}
.content {
  width: 100%;
  font-size: 1.2rem;
}
.content p {
  text-indent: 2rem;
}
.inputBox {
  height: 2rem;
  width: 90%;
  line-height: 2rem;
  font-size: 1rem;
  background-color: lightgrey;
  border-radius: 1.5rem;
  text-indent: 10px;
}
.inputArea {
  height: 6rem;
  width: 90%;
  background-color: lightgrey;
  border-radius: 0.2rem;
  font-size: 1rem;
}
.select-left {
  width: 50%;
  float: left;
}
.select-right {
  width: 50%;
  float: right;
}
.select {
  height: 5rem;
}
.submit {
  height: 4rem;
  width: 8rem;
}
.button {
  font-size: 1rem;
  border-radius: 2rem;
  background: -webkit-linear-gradient(
    left,
    rgb(94, 146, 204),
    rgb(133, 115, 180)
  ); /* Safari 5.1 - 6.0 */
  background: -o-linear-gradient(
    right,
    rgb(94, 146, 204),
    rgb(133, 115, 180)
  ); /* Opera 11.1 - 12.0 */
  background: -moz-linear-gradient(
    right,
    rgb(94, 146, 204),
    rgb(133, 115, 180)
  ); /* Firefox 3.6 - 15 */
  background: linear-gradient(
    to right,
    rgb(94, 146, 204),
    rgb(133, 115, 180)
  ); /* 标准的语法 */
  border: none;
}
</style>